import VueRouter from 'vue-router'
import MainPage from './components/MainPage.vue'
import SecondPage from './components/SecondPage.vue'


export default new VueRouter ({
    routes: [
        {
            path: '',
            component: MainPage
        },
        {
            path: '/secondPage',
            component: SecondPage
        },
    ],
    mode: "history"
})